nav{
    background: rgba(0,0,0,.3);
    display: block;
    width: max-content;
    padding: .7rem 1.7rem;
     z-index: 1;
     transform: translate(-50%);
     position: fixed;
    left: 50%;
    bottom: 2rem;
    gap: .8rem;
    backdrop-filter: blur(2px);
    display: flex;
    border-radius: 3rem;
    
}

nav a{
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(176, 179, 183, 0.5);
}

nav a.active{
    background:var(--color-bg);
    color: var(--color-light);
}