header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ========Cv and Talk btn */
.cv-talk{
  margin-top: 2.5rem;
  display: flex; 
  gap: 1.2rem; 
  justify-content: center;
}

/* =============SOCIALS icons========= */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::before{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* =============Header Img========= */
.me img{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ==============SCROLL DOWN ==================== */
.scroll-down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: .9rem;
}

/* =================MEADIA QUERY for MEADIAM DEVICES=========== */
@media screen and (max-width:1024px) {
   .header{
    height: 68vh;
   }
  }

  /* =================MEADIA QUERY for SMALL DEVICES=========== */

  @media screen and (max-width:600px) {
       .header{height:100vh;}
       .header__socials,.scroll-down{
        display: none;
       }   
}